<template>
 <div class="search__bar">
     <input type="text" class="search__input" v-model="userName" v-on:keyup.enter="findUsers"  placeholder="Введите имя пользователя">
     <button class="search__btn" @click="findUsers">Найти</button>
 </div>
    
</template>

<script>
 import { mapGetters, mapMutations, mapActions } from "vuex";
export default {
    data(){
        return {
            userName:"",
        }
    },
    computed:{
    },
    methods:{
        ...mapActions(['fetchUser']),
        ...mapMutations(['userNameMut']),
        findUsers(){
            this.userNameMut(this.userName)
            this.fetchUser({userName: this.userName,sort:this.$store.state.sort});
            this.userName = ""
        },
   
    }
}
</script>

