<template>
<div class="users" v-show="$store.state.userInfo">
    <div class="user__wrap">
        <div class="user__left">
            <img :src="$store.state.userInfo.avatar_url" alt="" class="user__img">
            <a class="user__btn" :href="$store.state.userInfo.html_url">Посетить</a>
        </div>
        <div class="user__right">
            <p class="user__name">{{ $store.state.userInfo.login }}</p>
            <p class="user__reps">Репозиториев: <span>{{ $store.state.userInfo.public_repos }}</span></p>
            <p class="user__created">Создан:<span>{{ $store.state.userInfo.created_at }}</span></p>
            <p class="user__sub">Подписщиков:<span>{{ $store.state.userInfo.followers }}</span></p>
            <p class="user__subscribed">Подписок:<span>{{ $store.state.userInfo.following }}</span></p>
        </div>
    </div>
    <div class="sort__wrap">
        <h2 class="sort__h2">Сортировка</h2>
        <div class="sort__btns">
            <button class="sort__btn" :class="nameBtn" @click="sortName">Имя</button>
            <button class="sort__btn" :class="starBtn" @click="sortStar">Звезды</button>
            <button class="sort__btn" :class="createdBtn" @click="sortCreated" >Дата</button>
        </div>
    </div>
</div>
</template>

<script>
import { mapMutations, mapActions } from "vuex";
export default {
    data(){
        return {
            nameBtn:"clicked",
            starBtn:"",
            createdBtn:"",
        }
    },
    methods:{
        ...mapMutations(['sortBtns']),
        ...mapActions(['fetchUser']),
        sortName(){
            this.nameBtn = "clicked"
            this.starBtn = this.createdBtn = ""
            
            
            this.sortBtns('name')
            this.fetchUser({userName: this.$store.state.userName, sort:this.$store.state.sort});
        },
        sortStar(){
            this.starBtn = "clicked"
            this.nameBtn = this.createdBtn = ""
            
            this.sortBtns('stars')
            this.fetchUser({userName: this.$store.state.userName, sort:this.$store.state.sort});
        },
        sortCreated(){
            this.createdBtn = "clicked"
            this.starBtn = this.nameBtn = ""
            
            this.sortBtns('created')
            this.fetchUser({userName: this.$store.state.userName, sort:this.$store.state.sort});
        }
    }
};
</script>

