import { createStore } from "vuex";

const store = createStore({
   
    actions:{
        async fetchUser({commit},info ) {
            const res = await fetch(`https://api.github.com/users/${info.userName}`)
            const userInfo = await res.json()
            
            const resp = await fetch(`https://api.github.com/users/${info.userName}/repos?sort=${info.sort}&per_page=5`)
            const reposes = await resp.json()
            
            let a = userInfo.message
            if (a === 'Not Found') {
                commit("vShowMut", false)
                alert('такого пользователя не существует')
            }else{
            commit("upgUserInfo",userInfo)
            commit("upgreposes", reposes)
            commit("vShowMut", true)
            }
        },
        
        
    },
        
    mutations:{
        upgUserInfo(state, userInfo){
            state.userInfo = userInfo
        },
        upgreposes(state, reposes){
            state.reposes = reposes
        },
        sortBtns(state, sort){
            state.sort = sort 
        },
        vShowMut(state,bool){
            state.vShow = bool
        },
        userNameMut(state,Name){
            state.userName = Name
        }
        
    },
    state:{
        userInfo:[],
        reposes:[],
        sort:"name",
        vShow:false,
        userName:""
    },
    getters:{
    },
    
    })

export default store